body {
  margin: 0;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Makes the links to different parts of the page smoothly scroll there as opposed to jumping there */
* {
  scroll-behavior: smooth;
}

/*  The below is for the Back and Forth image on the landing page */
#landing_container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: repeating-linear-gradient(
    45deg,
    #7f0000 0%,
    #212121 60%,
    #7f0000 100%
  );
  background-size: 300%;
  animation: bg-animation 8s infinite alternate;
}
@keyframes bg-animation {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

.strikeThrough {
  position: relative;
  z-index: 1;
}
.strikeThrough:before {
  content: "";
  display: block;
  width: 100px;
  height: 7px;
  background: #b8b804;
  left: 0;
  top: 60%;
  position: absolute;
  z-index: -1;
}

.strikeThrough2 {
  position: relative;
  z-index: 1;
}
.strikeThrough2:before {
  content: "";
  display: block;
  width: 100px;
  height: 7px;
  background: #880505;
  left: 0;
  top: 60%;
  position: absolute;
  z-index: -1;
}

/* This sets the sticky attribute to anything so it will stick to the top of the page : used in navbar */
#sticky {
  position: sticky;
  top: 0;
  z-index: 1;
}
#navbar {
  overflow: hidden;
  backdrop-filter: blur(50px);
  margin: auto;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: left;
}
/* sx={{ textAlign: "left", marginLeft: "10vw" }}> */

.anchor {
  display: block;
  height: 60px; /*same height as header*/
  margin-top: -60px; /*same height as header*/
  visibility: hidden;
}

#footer {
  overflow: hidden;
}

#profileImage {
  width: 100%;
}

#logoImg {
  max-width: 80px;
  height: auto;
}

#logoContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Sets the styles of all links */
a {
  text-decoration: none;
  font-weight: normal;
}

.alignIcon {
  vertical-align: middle;
}
